import React, { useState } from "react";

const RouterStore = ({ element }) => {
  const context = useState(null);

  return <RouterContext.Provider value={context}>{element}</RouterContext.Provider>;
};

// You can delete this file if you're not using it
export const wrapRootElement = RouterStore;
